import React from "react";
import "../Styles/Display.css";
// import all desired components and lists
import LockScreen from "./LockScreen";
import Navbar from "./Header";
import Menu from "./Menu";
import Music from "./Music";
import Songs from "./Songs";
import Settings from "./Setting";
import Playing from "./Playing";
import Themes from "./Themes";
import Wallpaper from "./Wallpaper";
import WheelColor from "./WheelColor";

class Display extends React.Component {
  render() {
    const {
      active,
      currentMenu,
      menuItems,
      musicItems,
      songItems,
      playing,
      songIndex,
      audio,
      songUrl,
      songImgUrl,
      noty,
      setNoty,
      notifyText,
      wallpaper,
      WallpaperItems,
    } = this.props;

    //  in display we will render different components as per the active menu or currentmenu active
    // Key for displaying menu
    // {-2: lock screen, -1 : main menu, 0 : now playing, 1: music menu, 2,5,6 : dummy menu, 3: setings menu,4:songs menu, 7:music playing, 8 :themes menu, 9:wheel color menu, 10:wallpaper menu}
    return (
      <div
        className="display"
        style={{ backgroundImage: `url(${WallpaperItems[wallpaper]})` }}
      >
        {/* render navbar for sure and passed proper props */}
        <Navbar
          noty={noty}
          setNoty={setNoty}
          playing={playing}
          notifyText={notifyText}
        />
        {/* if currently the menu is -2 render lockscreen */}
        {currentMenu === -2 && <LockScreen />}
        {/* screen for selected song image*/}
        {currentMenu === -1 && (
          <Menu songImgUrl={songImgUrl} menuItems={menuItems} active={active} />
        )}
        {currentMenu === 1 && <Music musicItems={musicItems} active={active} />}
        {/* after selecting games make screen empty div with a simple text written */}
        {currentMenu === 2 && (
          <div className="blank-div">
            <h1 className="empty-text">Games Coming Soon</h1>
          </div>
        )}
        {currentMenu === 3 && <Settings active={active} />}
        {currentMenu === 4 && <Songs songItems={songItems} active={active} />}
        {currentMenu === 5 && (
          <div className="blank-div">
            <h1 className="empty-text">Artists</h1>
          </div>
        )}
        {currentMenu === 6 && (
          <div className="blank-div">
            <h1 className="empty-text">Albums</h1>
          </div>
        )}
        {(currentMenu === 0 || currentMenu === 7) && (
          <Playing
            songImgUrl={songImgUrl}
            audio={audio}
            songUrl={songUrl}
            playing={playing}
            songIndex={songIndex}
            songItems={songItems}
          />
        )}
        {currentMenu === 8 && <Themes active={active} />}
        {currentMenu === 9 && <WheelColor active={active} />}
        {currentMenu === 10 && <Wallpaper active={active} />}
        {/* <LockScreen /> */}
      </div>
    );
  }
}
export default Display;
