import React from "react";
// import all required files and components
import "./Styles/App.css";
import Cover from "./Components/Cover";

// importing wallpapers from static
import babyYodaWallpaper from "./static/babyYoda.jpg";
import earthWallpaper from "./static/earth.jpg";
import earth2020Wallpaper from "./static/earth2020.jpg";
import jokerWallpaper from "./static/joker.png";
// import songs from static
import song1 from "./static/songs/Scam-1992BGM.mp3";
import song2 from "./static/songs/pirates-jack-bgm.mp3";
import song3 from "./static/songs/Tum-Tak.mp3";

// import songs cover images
import song1Img from "./static/song1Img.jpg";
import song2Img from "./static/song2Img.jpg";
import song3Img from "./static/song3Img.jpg";

// returning app as a class component to render
class App extends React.Component {
  // setting state here in app in order to pass properties as props to each child component
  constructor() {
    super();
    // setting almost every functionalities in app component in order to pass as props to desired child component
    this.state = {
      // wallpapers list
      WallpaperItems: [
        babyYodaWallpaper,
        earthWallpaper,
        earth2020Wallpaper,
        jokerWallpaper,
      ],
      songItems: ["Scam-1992-bgm", "jack-sparrow-bgm", "Tum-Tak"], //songs
      songItemsUrl: [song1, song2, song3], //songs list url to songs
      songImgItemsUrl: [song1Img, song2Img, song3Img], //song images list
      songUrl: song1, //current song url
      // song image url
      songImgUrl: song1Img,
      theme: "rgb(113, 126, 243)", //current body theme
      audio: new Audio(song1), //current audio file
      wheelColor: "white", //current wheel color
      // initially wallpaper id
      wallpaper: 3,
      active: 0, //Active list item
      menuItems: ["Now Playing", "Music", "Games", "Settings"], //menu Items
      musicItems: ["All Songs", "Artist", "Albums"], //Items in music
      songIndex: 0, //current song index
      lengthMenuKey: { "-1": 3, 1: 2, 4: 2, 8: 4, 3: 2, 9: 3, 10: 2 }, //length of a particular menu
      menuMapping: { "-1": [0, 1, 2, 3], 1: [4, 5, 6], 3: [8, 9, 10] }, //which menu can be rendered by key menu
      currentMenu: -2, //current menu which is lockscreen initially
      navigationStack: [], //Used for navigation forward and backward
      playing: false, // initially not playing
      noty: false, // to show notification or not
      notifyText: "Changes Done", //notification text on successful change
    };
  }

  // function for setting notification false after a minute
  setNoty = () => {
    this.setState({ noty: false });
    return;
  };
  // function for changing or setting themes for ipod body
  setTheme = (id) => {
    let theme = "";
    if (id === 0) {
      theme = "#FDDCD7";
    } else if (id === 1) {
      theme = "rgb(113, 126, 243)";
    } else if (id === 2) {
      theme = "#F5DDC5";
    } else if (id === 3) {
      theme = "gray";
    } else if (id === 4) {
      theme = "black";
    }
    this.setState({ theme: theme, noty: true, notifyText: "Theme Changed" });
    return;
  };

  //function for changing wheelcolor
  setWheelColor = (id) => {
    let wheelColor = "";
    if (id === 0) {
      wheelColor = "black";
    } else if (id === 1) {
      wheelColor = "white";
    } else if (id === 2) {
      wheelColor = "brown";
    } else if (id === 3) {
      wheelColor = "#3D5AFE";
    }
    this.setState({
      wheelColor: wheelColor,
      noty: true,
      notifyText: "Wheel Color Changed",
    });
    return;
  };

  //function for setting wallpaper for body
  setWallpaper = (id) => {
    this.setState({
      wallpaper: id,
      noty: true,
      notifyText: "Wallpaper Changed",
    });
    return;
  };

  // function for changing song from playing music menu
  chagePlayingSongFromMusicMenu = (id, navigationStack) => {
    const songUrl = this.state.songItemsUrl[id];
    const songImgUrl = this.state.songImgItemsUrl[id];
    this.state.audio.pause();
    this.setState(
      {
        currentMenu: 7,
        songUrl: songUrl,
        navigationStack: navigationStack,
        active: 0,
        playing: true,
        songIndex: id,
        audio: new Audio(songUrl),
        songImgUrl: songImgUrl,
      },
      () => {
        this.state.audio.play();
      }
    );
    return;
  };

  // function for updating active menu on rotating over wheel
  updateActiveMenu = (direction, menu) => {
    if (
      menu !== -1 &&
      menu !== 1 &&
      menu !== 4 &&
      menu !== 8 &&
      menu !== 3 &&
      menu !== 9 &&
      menu !== 10
    ) {
      return;
    }
    let min = 0;
    let max = 0;

    max = this.state.lengthMenuKey[menu];
    // handling left or right rotation
    if (direction === 1) {
      if (this.state.active >= max) {
        this.setState({ active: min });
      } else {
        this.setState({ active: this.state.active + 1 });
      }
    } else {
      if (this.state.active <= min) {
        this.setState({ active: max });
      } else {
        this.setState({ active: this.state.active - 1 });
      }
    }
  };

  // function for changing MENU FORWARD on pressing center button using NAVIGATION STACK
  changeMenuForward = (id, fromMenu) => {
    const navigationStack = this.state.navigationStack.slice();

    if (
      fromMenu !== -2 &&
      fromMenu !== -1 &&
      fromMenu !== 0 &&
      fromMenu !== 1 &&
      fromMenu !== 3 &&
      fromMenu !== 4 &&
      fromMenu !== 7 &&
      fromMenu !== 8 &&
      fromMenu !== 9 &&
      fromMenu !== 10
    ) {
      return;
    }

    if (fromMenu === -2) {
      navigationStack.push(this.state.currentMenu);
      this.setState({
        currentMenu: -1,
        navigationStack: navigationStack,
        active: 0,
      });
      return;
    }

    if (fromMenu === -1) {
      navigationStack.push(this.state.currentMenu);
      this.setState({
        currentMenu: id,
        navigationStack: navigationStack,
        active: 0,
      });
      return;
    }

    if (fromMenu === 7 || fromMenu === 0) {
      this.togglePlayPause();
      return;
    }

    if (fromMenu === 8) {
      this.setTheme(id);
      return;
    }

    if (fromMenu === 9) {
      this.setWheelColor(id);
      return;
    }

    if (fromMenu === 10) {
      this.setWallpaper(id);
      return;
    }

    navigationStack.push(this.state.currentMenu);

    if (fromMenu === 4) {
      this.chagePlayingSongFromMusicMenu(id, navigationStack, fromMenu);
      return;
    }

    const currentMenuID = this.state.menuMapping[fromMenu][id];
    this.setState({
      currentMenu: currentMenuID,
      navigationStack: navigationStack,
      active: 0,
    });
  };

  // function for changing menu in backward direction
  changeMenuBackward = () => {
    const navigationStack = this.state.navigationStack.slice();
    if (this.state.currentMenu === -2) {
      return;
    } else {
      const prevId = navigationStack.pop();
      this.setState({
        currentMenu: prevId,
        navigationStack: navigationStack,
        active: 0,
      });
      return;
    }
  };
  // FUNCTION FOR : ON LONG PRESS OF FORWARD BUTTON, TRACKS ARE SEEKED FORWARD
  seekSongForward = (e) => {
    if (this.state.currentMenu === -2) {
      return;
    }
    if (this.state.playing === false) {
      return;
    }
    if (e.detail.interval < 250) {
      this.state.audio.pause();
      let songIndex = this.state.songIndex;
      if (songIndex === this.state.songItemsUrl.length - 1) {
        songIndex = 0;
      } else {
        songIndex++;
      }
      const songUrl = this.state.songItemsUrl[songIndex];
      const songImgUrl = this.state.songImgItemsUrl[songIndex];
      this.setState(
        {
          songIndex: songIndex,
          songImgUrl: songImgUrl,
          songUrl: songUrl,
          audio: new Audio(songUrl),
        },
        () => {
          this.state.audio.play();
        }
      );
    } else if (e.detail.interval > 250 && e.detail.interval < 10000) {
      const interval = e.detail.interval / 100;
      this.setState((prevState) => {
        prevState.audio.currentTime += interval;
        return prevState;
      });
    }
  };

  // FUNCTION FOR : ON LONG PRESS OF BACKWARD BUTTON , TRACKS ARE SEEKED BACKWARD
  seekSongReverse = (e) => {
    if (this.state.currentMenu === -2) {
      return;
    }
    if (this.state.playing === false) {
      return;
    }
    console.log(e.detail.interval);
    if (e.detail.interval < 250) {
      this.state.audio.pause();
      let songIndex = this.state.songIndex;
      if (songIndex === 0) {
        songIndex = this.state.songItemsUrl.length - 1;
      } else {
        songIndex--;
      }
      const songUrl = this.state.songItemsUrl[songIndex];
      const songImgUrl = this.state.songImgItemsUrl[songIndex];
      this.setState(
        {
          songIndex: songIndex,
          songImgUrl: songImgUrl,
          songUrl: songUrl,
          audio: new Audio(songUrl),
        },
        () => {
          this.state.audio.play();
        }
      );
    } else if (e.detail.interval > 250 && e.detail.interval < 10000) {
      const interval = e.detail.interval / 100;
      this.setState((prevState) => {
        prevState.audio.currentTime -= interval;
        return prevState;
      });
    }
  };

  // FUNCTION FOR : TOGGLE SONG PLAY AND PAUSE
  togglePlayPause = () => {
    if (this.state.currentMenu === -2) {
      return;
    }
    if (this.state.playing === true) {
      this.setState({ playing: false });
      this.state.audio.pause();
    } else {
      this.setState({ playing: true });
      this.state.audio.play();
    }
  };

  render() {
    const {
      audio,
      active,
      currentMenu,
      menuItems,
      musicItems,
      songItems,
      playing,
      songIndex,
      theme,
      songUrl,
      songImgUrl,
      noty,
      notifyText,
      wheelColor,
      WallpaperItems,
      wallpaper,
    } = this.state;
    return (
      <div className="App">
        <h1 style={{ marginLeft: 35 }}>My Pocket ipod</h1>
        <Cover
          noty={noty}
          setNoty={this.setNoty}
          notifyText={notifyText}
          songIndex={songIndex}
          active={active}
          menuItems={menuItems}
          musicItems={musicItems}
          currentMenu={currentMenu}
          changeMenuForward={this.changeMenuForward}
          changeMenuBackward={this.changeMenuBackward}
          updateActiveMenu={this.updateActiveMenu}
          togglePlayPause={this.togglePlayPause}
          seekSongForward={this.seekSongForward}
          seekSongReverse={this.seekSongReverse}
          songIndex={songIndex}
          playing={playing}
          songItems={songItems}
          theme={theme}
          audio={audio}
          songImgUrl={songImgUrl}
          WallpaperItems={WallpaperItems}
          wallpaper={wallpaper}
          wheelColor={wheelColor}
        />
        {/* <Wheel /> */}
      </div>
    );
  }
}

export default App;
