import React from "react";
import BatteryImg from "../static/battery.png";
import "../Styles/Header.css";
class Navbar extends React.Component {
  constructor() {
    super();
    this.state = {
      time: this.getCurrentTime(),
    };
    this.stateId = "";
  }
  //if there is no notification then iPod logo, time and battery icon else If there is a notification show it for 1 second and clear it
  componentDidMount() {
    const { noty } = this.props;
    if (noty === true) {
      return;
    }
    // set an interval of 60 seconds to update time
    this.stateId = setInterval(() => {
      this.setState({ time: this.getCurrentTime() });
    }, 60000);
  }

  componentDidUpdate() {
    const { setNoty, noty } = this.props;
    if (noty === true) {
      setTimeout(function () {
        setNoty();
      }, 1000);
    }
  }

  // Clear the update time interval
  componentWillUnmount() {
    const { noty } = this.props;
    if (noty !== true) clearInterval(this.stateId);
  }

  // for getting current time as string
  getCurrentTime() {
    const today = new Date();
    var time = today.getHours() + ":" + today.getMinutes();
    if (today.getMinutes() < 10) {
      // i.e time 11:08 instead if 11:8
      time = today.getHours() + ":0" + today.getMinutes();
    }
    console.log(time);
    return time;
  }

  render() {
    const { noty, notifyText, playing } = this.props;
    const { time } = this.state;
    return (
      <div className="bar">
        {
          <h5 className="heading">
            Nosta <i className="fas fa-wifi"></i>
          </h5>
        }
        {/* if we get noti as true via props we will show that for 1 sec */}
        {noty === true && <h5 className="notification">{notifyText}</h5>}
        {noty === false && <h3 className="time">{time}</h3>}
        {
          <div className="right-container-nav">
            {/* if song playing is true show play icon otherwise show pause icon in right corner of navbar */}
            {playing ? (
              <h5 className="play-pause-nav">
                <i className="fas fa-play"></i>
              </h5>
            ) : (
              <h5 className="play-pause-nav">
                <i className="fas fa-pause"></i>
              </h5>
            )}
            <img className="battery" src={BatteryImg} alt="Battery" />
          </div>
        }
      </div>
    );
  }
}

export default Navbar;
