import React from "react";
import Wheel from "./Wheel";
import Display from "./Display";
import "../Styles/Cover.css";

class Cover extends React.Component {
  render() {
    // getting functionalities as props from App component and passing again to each of display and wheel the required ones
    const {
      seekSongForward,
      seekSongReverse,
      togglePlayPause,
      active,
      updateActiveMenu,
      currentMenu,
      changeMenuBackward,
      changeMenuForward,
      menuItems,
      musicItems,
      songItems,
      playing,
      songIndex,
      audio,
      songUrl,
      songImgUrl,
      noty,
      setNoty,
      notifyText,
      theme,
      wheelColor,
      wallpaper,
      WallpaperItems,
    } = this.props;
    return (
      <div className="cover-container">
        <div className="cover" style={{ backgroundColor: theme }}>
          <Display
            WallpaperItems={WallpaperItems}
            wallpaper={wallpaper}
            songIndex={songIndex}
            playing={playing}
            active={active}
            musicItems={musicItems}
            menuItems={menuItems}
            currentMenu={currentMenu}
            songItems={songItems}
            audio={audio}
            songUrl={songUrl}
            songImgUrl={songImgUrl}
            noty={noty}
            setNoty={setNoty}
            notifyText={notifyText}
          />
          <Wheel
            theme={theme}
            wheelColor={wheelColor}
            active={active}
            menuItems={menuItems}
            currentMenu={currentMenu}
            changeMenuForward={changeMenuForward}
            changeMenuBackward={changeMenuBackward}
            seekSongReverse={seekSongReverse}
            seekSongForward={seekSongForward}
            togglePlayPause={togglePlayPause}
            updateActiveMenu={updateActiveMenu}
          />
        </div>
      </div>
    );
  }
}
export default Cover;
